import React from 'react'

import './styles/Footer.css'

const Footer = () => (
  <div className="splashdiv" id="footer">
    <div id="about-statement">
      <p>
        Jonathan Crawford is a multi-instrumentalist, composer, sound-designer, record producer and DJ based in San Francisco, CA.
      </p>
      <p>
        As an artist, his goal is to create music which explores exciting new territory from both a timbral and compositional point of view.
      </p>
      <p>
        He hopes to leave his audience with a stronger sense of narrative and possibility in their own lives.
      </p>
    </div>

  </div>
)

export default Footer



